import {AWidget} from "./abstract/a-widget";
import jquery = require("jquery");

/**
 * Um label que exibe uma descrição em forma de texto
 * 
 * @export
 * @class Label
 * @extends {AWidget}
 * @memberof module:mvcomponents/widget
 */
export class Label extends AWidget{
	constructor(p_text:string=""){
		super("label","");
		jquery(this.element)
			.text(p_text)
			.addClass("Label col-xs-12 col-sm-12");
	}
	/**
	 * Informa o texto do label 
	 * 
	 * @param {string} texto
	 * @returns {this}
	 */
	public setText(p_text:string):this{
		jquery(this.element).text(p_text);
		return this;
	}
}
